/* font familes */

/* source-sans-pro-200 - latin */
@font-face {
    font-family: 'Source Sans Pro';
    font-style: normal;
    font-weight: 200;
	font-display: swap;
    src: url('/fonts/Source_Sans_Pro/source-sans-pro-v13-latin-200.woff2') format('woff2'),
         url('/fonts/Source_Sans_Pro/source-sans-pro-v13-latin-200.woff') format('woff');
}

